.board {
    margin: 20px auto;
    max-width: 400px;
}
  
.row {
    display: flex;
}

.congratulations {
    position: fixed;
    bottom: 285px;
    margin-left: 50px;
    font-size: 2rem;
}

.icon {
    color: blue;
}

.draw {
    position: fixed;
    bottom: 285px;
    margin-left: 90px;
    font-size: 2rem;
    color: red;
}

.current_player {
    margin-left: -180px;
    position: fixed;
    bottom: 285px;
    font-size: 2rem;
}

.title {
    position: relative;
    bottom: -60px;
    color: #0072B5;
}

.square {
    font-size: 2em;
    font-weight: bold;
    height: 80px;
    line-height: 80px;
    text-align: center;
    width: 80px;
}
  
.square:hover {
    background-color: #eee;
}
  
.status {
    font-size: 1.5em;
    margin-bottom: 100px;
    text-align: center;
}
  
.reset {
    background-color: #4CAF50;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    font-size: 16px;
    margin-top: 70px;
    padding: 10px 20px;
    text-decoration: none;
    margin-left: 65px;
}

@media (max-width: 1024px) {
    .current_player {
        bottom: 200px;
    }

    .congratulations {
        bottom: 200px;
    }

    .draw {
        bottom: 200px;
    }
}

@media (max-width: 600px) {
    .board {
        max-width: 300px;
    }

    .current_player {
        bottom:260px;
        margin-left: -140px;
    }

    .congratulations {
        bottom: 260px;
    }

    .draw {
        bottom: 260px;
    }
    
    .square {
        height: 60px;
        line-height: 60px;
        font-size: 1.5em;
    }
    
    .reset {
        margin-left: 60px;
    }
}

@media (max-width: 400px) {
    .board {
        max-width: 200px;
    }

    .current_player {
        bottom: 220px;
        margin-left: -110px;
    }

    .congratulations {
        bottom: 220px;
        margin-left: 20px;
    }

    .draw {
        bottom: 220px;
        margin-left: 60px;
    }
    
    .square {
        height: 40px;
        line-height: 40px;
        font-size: 1em;
    }
    
    .status {
        font-size: 1em;
    }
    
    .reset {
        margin-top: 30px;
        margin-left: 40px;
    }
}